import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';

// 
export function changePasswordOnFirstLogin(data) {
  return request({
    url: '/v1/changePasswordOnFirstLogin',
    method: 'post',
    data: data,
  });
}

// 
export function queryOrgOperator(data) {
  return request({
    url: '/v1/orgOperator/query',
    method: 'post',
    data: data,
  });
}

// QA
export function queryQuestions(data) {
  return request({
    url: buildUrlWithObj('/v1/questions', data),
    method: 'get',
  });
}

// QA
export function queryOperatorQuestions(data) {
  return request({
    url: buildUrlWithObj('/operator/v1/questions', data),
    method: 'get',
  });
}

