import axios from 'axios';
import cookie from './cookie';
import router from '@/router';
import { preventReSubmit } from './request-prevent-resubmit';

import emitMsgBox, { emitMessageInfo } from '@/common/utils/exception-msgbox-utils';

const isDebug = true; // mock

// axios
const service = axios.create({
  //  baseURL  axios  URL url = baseURL + urlurlURL
  baseURL: process.env.VUE_APP_BASEAPI,
  // apolloURL: process.env.VUE_APOLLO_BASEURL,

  // 
  withCredentials: true,
  xsrfCookieName: 'O-XSRF-TOKEN',
  // 
  timeout: 1000 * 60, //
  headers: {
    'x-requested-with': 'XMLHttpRequest',
  },
});

preventReSubmit(service);

// 
service.interceptors.request.use(
  (config) => {
    let uToken = cookie.get('o-token');
    if (uToken) {
      config.headers['o-token'] = uToken;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  (response) => {
    const res = response.data;
    if (res && res.header && res.header.success === false && !response.config.ignoreError) {
      emitMsgBox(res);
    }
    if (response.config.needHeader) {
      res.headers = response.headers;
    }
    return res;
  },
  (error) => {
    // 
    if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout of') !== -1) {
      emitMessageInfo({
        message: 'Apply Submited,please check it later.',
        customClass: 'message-project-info',
      });
    } else {
      if (error.response) {
        if (error.response.status === 410 || error.response.status === 401) {
          router.push({ path: '/login' });
        } else if (error.response.status === 400) {
          emitMsgBox(error.response.data.cause);
        } else if (error.response.status === 403) {
          emitMsgBox(error.response.data);
        } else {
          const res = error.response.data;
          if (res && res.header) {
            emitMsgBox(res);
          } else {
            emitMsgBox(error.message);
          }
        }
      } else {
        emitMsgBox(error.message);
      }
    }

    return Promise.reject(error);
  }
);

/**
 * mock
 */
const request = (opt) => {
  return new Promise((resolve, reject) => {
    try {
      if (opt.url === '/v1/login') {
        // token
        import('./cookie.js').then((cookie) => {
          const { set } = cookie.default;
          set('o-token', '8F7CEED4A1CA14E6D134A35EF0C7A877C996D489B7B4F91A');
        });
      }
      import('./request-mock-debug.js').then((data) => {
        let impotRequest = data.default;
        resolve(impotRequest(opt));
      });
    } catch (e) {
      reject(e);
    }
  });
};

export default isDebug ? request : service;
