// IREQ02838591 Fintech2.0 DFR-TSC-15:,

const URL_LIST = [
  '/fintech/v1/customer/addCustomer',
  '/fintech/v1/spOperator/create',
  '/fintech/v1/orgOperator/create',
  '/fintech/v1/organization/create',
  '/fintech/v1/group/create',
  '/fintech/v1/till/create',
  '/fintech/v1/groupMember/addGroupMember',
];
const REQUEST_MAP = {};

function generateRandomSessionId() {
  const timestamp = Date.now();
  const rand = Math.floor((1 + Math.random()) * 100000000)
    .toString()
    .substring(0, 8);
  return timestamp + rand;
}

// requestIdrequestIdrequestId
export function preventReSubmit(service) {
  service.interceptors.request.use((config) => {
    const url = config.url;
    if (URL_LIST.includes(url)) {
      const data = config.data;
      if (!REQUEST_MAP[url]) {
        data.requestId = generateRandomSessionId();
        REQUEST_MAP[url] = data.requestId;
      } else {
        data.requestId = REQUEST_MAP[url];
      }
    }
    return config;
  });

  service.interceptors.response.use(
    (response) => {
      const url = response.config.url;
      if (REQUEST_MAP[url]) {
        REQUEST_MAP[url] = undefined;
      }
      return response;
    },
    (error) => {
      const url = error.config.url;
      if (error.request && REQUEST_MAP[url]) {
        REQUEST_MAP[url] = undefined;
      }
      return Promise.reject(error);
    }
  );
}
